import { newCarList } from "../../../api/mall.js"
// let isCanLoad = true;
export default {
	data() {
		return {
			storeId:'',// 门店id
			carList:[],
			isShowloading:true,
			scrollNodata:false,
			isCanLoad:true,
			page:1
		}
	},
	onLoad(ops) {
		if(ops.storeId == "undefined"){
			this.storeId = uni.getStorageSync('ssssId')
		}else{
			this.storeId = ops.storeId
		}
		this.getNewCarList()
	},
	onReachBottom() {
		this.getNewCarList()
	},
	methods: {
		getNewCarList(){
			console.log(12333)
			if(!this.isCanLoad) {return}
			this.isCanLoad = false
			newCarList({
				data:{
					store_id:this.storeId,
					page_no:this.page,
					page_size:10
				} 
			}).then(res=>{
				if(res.data.code == 0) {
					let list = res.data.result.cars || []
					for (let i in list) { 
						if(list[i].tags != '') {
							list[i].tags = list[i].tags.split(',')
						} 
					}
					if(list.length == 10) {
						this.page++
						this.isCanLoad = true
					}
					if(list.length < 10) {
						this.isShowloading = false
					}
					if(list.length < 10 && this.page > 1) {
						this.scrollNodata = true
					}
					this.carList = [...this.carList , ...list]
				}
			})
		},
		// 打开车辆详情
		openCarDetail(id){
			uni.navigateTo({
				url:`/pages/mall/newcar-detail/newcar-detail?carId=${id}&ssssId=${this.storeId}`
			})
		}
	}
}
